import React, {useState} from 'react'
import Navbar from './navbar'
import './styles/works.scss'
import $ from 'jquery'

export default function Works (){

    const works  = [

        {
            nome: 'Pizza Delivery App',
            img: 'screencapture-localhost-3000-2020-01-31-19_16_45.png',
            github: 'https://github.com/BatistaTony/PizzaDeliveryApp',
            descricao: 'App para compra de pizza e entrega ao dimicilio, tem uma parte cliente para o pedido e adimnistrador para receber o pedido.',
            tech: [
            'ReactJS',
            'NodeJS',
            'MongoDB',
            'ExpressJS'    
            ]
        },
        {
            nome: 'IwannaDJ',
            img: 'screenshot_20200127_211024.png',
            github: 'https://github.com/BatistaTony/IwannaDJ',
            descricao: 'App para  encontrar um DJ com as condições que o cliente tem como dinheiro e materias de musicas e escolher o melhor DJ para tocar na sua festa.',
            tech: [
                'ReactJS',
                'NodeJS',
                'MongoDB',
                'ExpressJS'    
                ]
        },
        {
            nome: 'BarberShop Tony',
            img: 'screenshot_20200127_210855.png',
            github: 'https://github.com/BatistaTony/BarbershopTony',
            descricao: 'App pra reserva de corte de cabelo.',
            tech: [
                'ReactJS',
                'NodeJS',
                'MongoDB',
                'ExpressJS'    
                ]
        },
        {
            nome: 'Cloudssongs',
            img: 'screenshot_20200131_190611.png',
            github: 'https://github.com/BatistaTony/Cloudssongs',
            descricao: 'App para guardar as suas musicas favoritas na net e ouvir em qualquer lugar.',
            tech: ['Node',
            'Template Engine Pug',
            'MongoDB','ExpressJS']
        },
        {
            nome: 'Sis. Gestão da Agencia de Viagem',
            img: 'screenshot_20190810_180727.png',
            github: 'https://github.com/BatistaTony/SGAV',
            descricao: 'Sistema de gestão focado na venda de bilhetes e controle da venda dos mesmos.',
            tech: ['C#',
            'MYSQL',
            'BUNIFU FRAMEWORK(DESIGN)',]
        },
        {
            nome: 'Hotel Paraiso',
            img: 'screenshot_20200131_191052.png',
            github: 'https://github.com/BatistaTony/Hotel',
            descricao: 'Site estático do hotel Paraiso para publicidade e para conhecer melhor o hotel.',
            tech: ['HTML',
            'CSS']
        }
        
    ]

    const techs = [
        'PHP',
        'ReactJS',
        'NodeJS',
        'MongoDb',
        'MYSQL',
        'C#',
        'SASS',
        'MOCKPLUS',
        'TEMPLATE ENGINE'
    ]

    const [cWork, setWork] = useState({
        nome: '',
        img: '',
        github:'',
        descricao:'',
        tech: []
    })

    

    const showTech  = (w)=> {
        setWork(w)
       $('.overlay').addClass('overlayAn')
    }

    const close = ()=> {
        $('.overlay').removeClass('overlayAn')
    }

    return (
        <div className="works_0 works">

                <Navbar item={'works'}/>

                <h1 className="titl_wrk">WORKS <div></div></h1>

                <div className="overlay">

                        <button onClick={close} className="exit_singleWrk">X</button>
                        <div className="singleWork">
                            <img src={"img/"+cWork.img} alt="" />
                            <div className="info">
                            <h1>{cWork.nome}</h1>
                            <p>{cWork.descricao}</p>
                            <a href={cWork.github}>{cWork.github}</a>
                                <ul>
                                    {cWork.tech.map((t,key)=>(
                                        <li key={key}>{t}</li>
                                    ))}
                                    

                                    </ul>
                                    
                                </div>
                            </div>
                    </div>

                <div className="works_1">


                    <div className="techs">
                        <ul>
                            {techs.map((t,key)=>(
                                <li key={key}>{t}</li>
                            ))}
                            </ul>
                        </div>

                    <ul className="lista">

                        {works.map((w, key)=>(
                             <li key={key}>
                                <img onClick={()=>showTech(w)} src={"img/"+w.img} alt="" />
                                <div>
                                    <h1>{w.nome}</h1>
                                    <a href={w.github}   target="_blank" rel="noopener noreferrer"><img src="img/icons8_github_48px.png" alt="github" /></a>
                                </div>
                            </li>
                        ))}

                        </ul>
                    </div>

            </div>
    )
}