import React from 'react'
import './styles/navbar.scss'
import $ from 'jquery'
import {Link} from 'react-router-dom'

export default function Navbar (props){
    
    var x = false

    const gotTo = (size)=>{
        $('html, body').animate({scrollTop:size+'px'})
        $('.btn').removeClass('openmenu')
        $('.submenu').css({display:'none'})
        $('.submenu').animate({'margin-left':'100%'}, 500)
        $('.line').css({'background-color':'black'})
        x = false
    }

   
    //var isMobile = window.matchMedia("only screen and (max-width:700px)").matches

    

    const OpenMenu = ()=>{

        if(x){

            $('.btn').removeClass('openmenu')
            $('.line').css({'background-color':'black'})
            $('.submenu').animate({'margin-left':'100%'}, 500)
            x = false
            
        }else{
            $('.submenu').animate({'margin-left':'0%'}, 500)
            $('.btn').addClass('openmenu')
            $('.line').css({'background-color':'white'})
            $('.submenu').css({display:'flex'})
            x = true
        }
    }

   

    return (
        <div className="navbar">

               
                <div className="submenu">           
                        <ul>
                            <li onClick={()=>gotTo(0)}><Link to="/home">HOME</Link></li>
                            {!props.home ? 
                              <li><Link to="/works">WORKS</Link></li>
                            : <li onClick={()=>gotTo(680)}>WORKS</li>
                            }
                            <li><Link to="/about">ABOUT</Link></li>
                            {!props.home ?
                            <li><Link className={props.item === 'about'?'actItem':''} to="/about">CONTACT</Link></li>
                            : <li onClick={props.home ? ()=>gotTo(2100): null}>CONTACT</li>}
                        </ul>
                    </div>

                <div className="leftSide">
                    <div className="logo">
                        <h1>B</h1>
                    </div>

                    <div className="menu">
                        <ul>
                            
                            
                            <li onClick={()=>gotTo(0)} ><Link  className={props.item === 'home'?'actItem':''}   to="/home">HOME</Link></li> 

                            {!props.home ? 
                              <li><Link   className={props.item === 'works'?'actItem':''} to="/works">WORKS</Link></li>
                            : <li onClick={()=>gotTo(680)}>WORKS</li>
                            }
                            <li><Link className={props.item === 'about'?'actItem':''} to="/about">ABOUT</Link></li>
                            
                            {!props.home ?
                            <li><Link className={props.item === 'about'?'actItem':''} to="/about">CONTACT</Link></li>
                            : <li onClick={props.home ? ()=>gotTo(2100): null}>CONTACT</li>}

                        </ul>
                    </div>

                    
                    
                    <div onClick={OpenMenu} className="btn">
                        <div className="line"></div>
                        <div className="line"></div>
                        <div className="line"></div>
                    </div>


                </div>

                <div className="div_btn">
                    <button className="btn_navbar"><a href="cv.pdf"   target="_blank" rel="noopener noreferrer"> MY CV</a></button>
                </div>
            </div>
    )
}